<template>
  <section>
    <ul class="category-list">
      <li v-for="(category, index) in categories" :key="index">
        <v-btn x-large @click="openMenu(category.division)" color="primary" class="category-item">{{ category.name }}</v-btn>
      </li>
    </ul>
  </section>
</template>

<script>
import { EventBus } from '../main';
export default {
  data() {
    return {
      categories: [
        { name: '세트', division: 'set' },
        { name: '버거', division: 'burger' },
        { name: '사이드', division: 'side' },
        { name: '음료', division: 'drink' },
      ],
    };
  },
  methods: {
    openMenu(category) {
      EventBus.$emit('sendMenuCategory', category);
      console.log('보낸 카테고리: ', category);
    },
    startRecognition() {
      const recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
      recognition.lang = 'ko-KR';
      recognition.start();

      recognition.onresult = (event) => {
        const speechResult = event.results[0][0].transcript;
        console.log('Result:', speechResult);

        if (speechResult.includes('세트')) {
          this.openMenu('set');
        } else if (speechResult.includes('버거') || speechResult.includes('햄버거 단품')) {
          this.openMenu('burger');
        } else if (speechResult.includes('사이드')) {
          this.openMenu('side');
        } else if (speechResult.includes('음료')) {
          this.openMenu('drink');
        }
      };

      recognition.onerror = (event) => {
        console.error('Speech recognition error:', event);
      };
    },
  },
  mounted() {
    this.startRecognition();
  },
};
</script>

<style scoped>
.category-list {
  /* 카테고리 목록 스타일 */
  display: flex;
  justify-content: center;
  list-style-type: none;
  padding: 0;
}

.category-item {
  /* 카테고리 버튼 스타일 */
  flex: 1;
  width: 23.5em;
  user-select: none; /* 텍스트 드래그 방지 */
  margin-right: 0.1em;
}
</style>
